<template>
  <div class="login-page">
    <div class="breadcrumb">
      <typewriter typeSpeed="50" :phrases="breadcrumb" numOfLoops="1"></typewriter>
    </div>
    <div class="wrapper">
      <header class="page-header">
        <h1 class="title">LOGIN</h1>
        <div class="desc">
          <router-link to="/login">Send me an email</router-link>&nbsp;to get access and
          <typewriter typeSpeed="70" eraseSpeed="40" :phrases="phrases" delay="2500"></typewriter>
        </div>
      </header>

      <form class="login-form" @submit.prevent="loginUser({email:email, password:password})">
        <div>
          <label for="email">
            <typewriter typeSpeed="50" :phrases="['email']" numOfLoops="1" delay="2000"></typewriter>
          </label>
          <input type="email" id="email" v-model="email" />
        </div>
        <div>
          <label for="password">
            <typewriter typeSpeed="50" :phrases="['password']" numOfLoops="1" delay="2500"></typewriter>
          </label>
          <input type="password" v-model="password" />
        </div>
        <div>
          <button type="submit">Sign in</button>
        </div>
        <transition name="bounce">
          <p v-if="error" class="error">{{error}}</p>
        </transition>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Typewriter from "@/components/TypeWriter.vue";
export default {
  name: "Login",
  components: { Typewriter },
  data() {
    return {
      email: "",
      password: "",
      breadcrumb: [":~ Init > login"],
      phrases: ["view hidden content.", "download my cv."]
    };
  },
  methods: {
    ...mapActions(["loginUser"])
  },
  computed: {
    ...mapState(["error"])
  }
};
</script>

<style lang="scss">
.login-page {
  position: relative;
  padding-top: 6rem;
  @media screen and (max-width: 767px) {
    padding-top: 1rem;
  }
  .page-header {
    margin-bottom: 4rem;
    .desc {
      font-size: 0.9rem;
      width: 100%;
      max-width: 500px;
      margin-left: auto;
      margin-right: auto;
      text-align: left;
      .typewriter {
        display: inline;
        line-height: 1.3rem;
      }
    }
  }
  .wrapper {
    width: 100%;
    text-align: center;
  }
  .login-form {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    .error {
      font-size: 0.8rem;
      color: red;
      margin-top: 1rem;
    }
    input {
      width: 100%;
      display: inline-block;
      margin-bottom: 1.5rem;
      padding: 1rem;
      background-color: white;
      border: 0;
      border-bottom: 1px solid $main-color;
      font-family: "Cousine", monospace !important;
      outline: none !important;
    }
    label {
      display: block;
      text-align: left;
    }
    button {
      font-family: "Cousine", monospace !important;
      color: white;
      padding: 0.8rem 4rem;
      background-color: $main-color;
      font-size: 1rem;
    }
  }
  .bounce-enter-active {
    animation: bounce-in 0.5s;
  }
  .bounce-leave-active {
    animation: bounce-in 0.5s reverse;
  }
  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }
}
</style>